import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ServiceService } from '../../service.service';
import { Router } from '@angular/router';
import { AlertService } from '../alert/alert.service';

@Component({
  selector: 'app-create-device',
  templateUrl: './create-device.component.html',
  styleUrls: ['./create-device.component.scss']
})
export class CreateDeviceComponent implements OnInit {
  form: FormGroup;
  submitted = false;
  constructor(private cd: ChangeDetectorRef, private formBuilder: FormBuilder,private service : ServiceService,private router:Router,
    private alertService: AlertService) {
    this.form = this.formBuilder.group({
      name: ['',Validators.required],
      serialNo: ['', Validators.required],
      description: ['', Validators.required],
    });
  }

  ngOnInit(): void {
  }
  get name() {
    return this.form.get('name');
  }

  get serialNo() {
    return this.form.get('serialNo');
  }
  get description() {
    return this.form.get('description');
  }


  createDevice() {
    this.submitted = true;


        // stop here if form is invalid
        if (this.form.invalid) {
            return;
        }
    const userData = this.form.value;
    console.log(this.form.value);
    const { name, serialNo,description } = this.form.value;
    this.service.addDevice(name,serialNo,description).subscribe(
      data => {
        console.log('create')
       console.log(data)
        if(data.status=="success")
        {
          this.alertService.success('Device added successfully', { keepAfterRouteChange : true });
          this.router.navigate(['/devicelist']);
        }
        else{
          console.log('error');
        }
      },
      error => {
        //this.errorMessage = error.message;
        if(error.error.status == "error") {
          window.localStorage.setItem("token", '');
        //  this.router.navigate(['/signin']);
        }
      });

  }

}
