import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {HttpHeaders} from '@angular/common/http';
import { from, Observable, Subject } from 'rxjs';
import { environment } from '../environments/environment';
@Injectable({
  providedIn: 'root',
})
export class ServiceService {
  Url: string;
  SharingData = new Subject();
  companyDetails: any;
  constructor(private http: HttpClient) {}

  login(username, password) {
    return this.http.post<any>(
      `${environment.apiUrl}/api/v1/iot/company/login/`,
      { email: username, password: password }
    );
  }

  getDeviceList() {
    let auth_token = window.localStorage.getItem("token");

     let data= window.localStorage.getItem("data");
     this.companyDetails = JSON.parse(data);
    const headers = new HttpHeaders({
      'Content-Type': 'application/json',
      Authorization: `Bearer ${auth_token}`,
    });

    const requestOptions = { headers: headers };
    return this.http.get<any>(
      `${environment.apiUrl}/api/v1/iot/company/getdevicemodels/`,
      requestOptions
    );
  }
  addDevice(name, serial,description) {
    let auth_token = window.localStorage.getItem("token");


    const headers = new HttpHeaders({
      'Content-Type': 'application/json',
      Authorization: `Bearer ${auth_token}`,
    });

    const requestOptions = { headers: headers };
    return this.http.post<any>(
      `${environment.apiUrl}/api/v1/iot/company/add/devicemodel/`,
      { model_name: name, model_serial_no: serial, model_description: description},{headers}
    );
  }
  getFirmware(id) {
    let auth_token = window.localStorage.getItem("token");
    const headers = new HttpHeaders({
      'Content-Type': 'application/json',
      Authorization: `Bearer ${auth_token}`,
    });
    const requestOptions = { headers: headers };
    return this.http.get<any>(
      `${environment.apiUrl}/api/v1/iot/device/ota/list/firmware/${id}/?page=1&limit=1`,
      requestOptions
    );
  }
  disabledFirmware(uid,model_id) {
    let auth_token = window.localStorage.getItem("token");

    const headers = new HttpHeaders({
      'Content-Type': 'application/json',
      Authorization: `Bearer ${auth_token}`,
    });

    const requestOptions = { headers: headers };
    return this.http.post<any>(
      `${environment.apiUrl}/api/v1/iot/device/ota/disable/firmware/`,
      { firmware_uid: uid, firmware_model_id: model_id},{headers, observe:'events'}
    );
  }
}
