import { Component, OnInit } from '@angular/core';
import { ServiceService } from '../../service.service';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from '../alert/alert.service';

@Component({
  selector: 'app-firmware',
  templateUrl: './firmware.component.html',
  styleUrls: ['./firmware.component.scss']
})
export class FirmwareComponent implements OnInit {

noheader:boolean;
  firmwareDetails : any[] = [];
  constructor(private service : ServiceService,private router:Router,private alertService: AlertService,
    private activatedRoute: ActivatedRoute) {

   }

  ngOnInit(): void {
    this.getFirmwareDetails();
  }

  getFirmwareDetails() {
    let id =  this.activatedRoute.snapshot.paramMap.get('id')
    console.log(id)
    this.service.getFirmware(id).subscribe(
      data => {
        console.log(data)
        if(data.status=="success" && data?.data?.firmwares?.length > 0)
        {

          this.firmwareDetails = data.data.firmwares;
          this.firmwareDetails.forEach(function(result, index) {
            let date =new Date(result.created_at);
            let release = new Date(result.releaseDate)
           result.created_at=[date.getFullYear(), date.getMonth()+1, date.getDate()].join('/');
           result.releaseDate = [release.getFullYear(), release.getMonth()+1, release.getDate()].join('/');
          });
          console.log(this.firmwareDetails)
        }
        else{
          this.alertService.success('No Firmeware found', { keepAfterRouteChange : true });
          console.log('error')
          this.noheader = true;
        }
      },
      error => {
        console.log(error)

      });
  }
  disabledFirmware(event: Event) {
    let uid = this.firmwareDetails[0]?.uid;
    let model_id = this.firmwareDetails[0]?.model_id;
  this.service.disabledFirmware(uid,model_id).subscribe(resp => {
   // console.log(resp);
    this.getFirmwareDetails();
  },error => {
    console.log(error)
  });
    event.preventDefault();
  }
}
