import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SigninComponent } from './pages/signin/signin.component';
import { DeviceListComponent } from './pages/device-list/device-list.component';
import { CreateDeviceComponent } from './pages/create-device/create-device.component';
import { FirmwareComponent } from './pages/firmware/firmware.component';
import { AuthGuard } from './auth.guard';

const routes: Routes = [
  { path: '', pathMatch: 'full', redirectTo : 'signin'},
  { path: 'home', loadChildren: () => import('./pages/home/home.module').then(m => m.HomeModule), pathMatch: 'full' },
  { path: 'signup', loadChildren: () => import('./pages/signup/signup.module').then(m => m.SignupModule), pathMatch: 'full' },
  { path: 'contactus', loadChildren: () => import('./pages/contactus/contactus.module').then(m => m.ContactusModule), pathMatch: 'full' },
  { path: 'forgot', loadChildren: () => import('./pages/forgot/forgot.module').then(m => m.ForgotModule), pathMatch: 'full' },
  { path: 'password-updated', loadChildren: () => import('./pages/password-updated/password-updated.module').then(m => m.PasswordUpatedModule), pathMatch: 'full' },
  { path: 'signin', component: SigninComponent, pathMatch: 'full' },
  { path: 'devicelist', component: DeviceListComponent, pathMatch: 'full',canActivate: [AuthGuard] },
  { path: 'createDevice', component: CreateDeviceComponent, pathMatch: 'full',canActivate: [AuthGuard] },
  { path: 'firmwaredetails', component: FirmwareComponent, pathMatch: 'full',canActivate: [AuthGuard] },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
